function sortByDate(a, b) {
  if (a.date < b.date) {
      return 1
  } else if (a.date === b.date) {
      return 0
  } else {
      return -1
  }
} 

export const articles = [
  ...v8_articles, 
  ...chrome_articles, 
  ...go_articles, 
  ...basic_javascript_articles,
  ...web_security_articles,
  ...javascript_design_pattern_articles,
  ...es6_articles,
  ...es7_8_articles,
  ...drawcharts_articles,
  ...elementui_articles,
  ...vue_articles,
  ...css_articles,
  ...babel_articles,
  ...handbyhand_articles,
  ...miscellaneous_articles,
  ...react_articles,
  ...es9_articles,
  ...http_articles,
  ...ts_articles,
  ...esnext_articles,
  ...nodejs_articles,
  ].sort(sortByDate)

  


